
.st0 {
    fill: none;
}
.st1 {
    fill: #666666;
}
.st2 {
    fill: #FFFFFF;
}
.st3 {
    fill: #FFFF00;
}
#Logo {
    position: absolute;
    left: 60px;
    top: 60px;
    width: auto;
    height: 100px;
    opacity: 0;
    transition: all .6s ease-out;
}
.loaded #Logo {
    opacity: 1;
    transition: all .6s ease-out;
}
#Logo .word,
#Logo .st1 {
    opacity: 0;
    transition: all .6s ease-out;
}
.loaded #Logo .word {
    opacity: 1;
    transition: all .6s ease-out;
    transition-delay: .6s;
}
.loaded #Logo .st1 {
    opacity: 1;
    transition: all .6s ease-out;
    transition-delay: .6s;
}
#Logo .st3 {
    stroke-width: 8px;
    stroke: #000;
    stroke-dasharray: 1000;
    stroke-dashoffset: 1000;
    -webkit-animation: dash 2s linear alternate forwards 1.6s;
            animation: dash 2s linear alternate forwards 1.6s;
}
#Logo .st4 {
    fill: transparent;
    stroke-width: 4px;
    stroke: #000;
    stroke-dasharray: 1000;
    stroke-dashoffset: 1000;
    -webkit-animation: dash 2s linear alternate forwards 1.6s;
            animation: dash 2s linear alternate forwards 1.6s;
}
@-webkit-keyframes dash {
from {
        stroke-dashoffset: 1000;
}
to {
        stroke-dashoffset: 0;
}
}
@keyframes dash {
from {
        stroke-dashoffset: 1000;
}
to {
        stroke-dashoffset: 0;
}
}



