
.project_list [class*=' col-'] {
    margin-bottom: 35px;
}
.project_list [class*=' col-'] a {
    display: block;
}
.project_list figure {
    margin: 0 auto;
    position: relative;
    overflow: hidden;
}
.project_list figure img {
    display: block;
    max-width: 100%;
    height: auto;
    width: 100%;
    -o-object-fit: cover;
       object-fit: cover;
    -o-object-position: center;
       object-position: center;
    max-height: 260px;
    min-height: 260px;
}
.project_list .caption {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    padding: 30px 0;
    background-color: rgba(0, 0, 0, 0.7);
    -o-ms-transform: translate(0%, 100%);
    -webkit-transform: translate(0%, 100%);
    transform: translate(0%, 100%);
    transition: -webkit-transform .25s ease-out;
    transition: transform .25s ease-out;
    transition: transform .25s ease-out, -webkit-transform .25s ease-out;
}
.project_list figure:hover .caption {
    -o-ms-transform: translate(0%, 0%);
    -webkit-transform: translate(0%, 0%);
    transform: translate(0%, 0%);
    transition: -webkit-transform .25s ease-out;
    transition: transform .25s ease-out;
    transition: transform .25s ease-out, -webkit-transform .25s ease-out;
}
.project_list .caption h2 {
    font-size: 15px;
    color: #EDED43;
    text-align: center;
    margin: 0;
    margin-bottom: 15px;
}
.project_list .caption h4 {
    font-size: 13px;
    color: #fff;
    text-align: center;
    margin: 0;
}




