
.inner_project_header {
    text-align: left;
    position: relative;
    padding-left: 20px;
}
.inner_project_header:before {
    content: '';
    position: absolute;
    left: 0;
    width: 5px;
    height: 100%;
    top: 0;
    bottom: 0;
    background: #EDED43;
}
.inner_project_header h2 {
    font-size: 26px;
    color: #666666;
    font-family: "IRANSANS BOLD";
    margin: 0;
    margin-bottom: 10px;
    line-height: normal;
    text-align: left;
}
.inner_project_header h3 {
    font-size: 21px;
    color: #999999;
    margin: 0;
    text-align: left;
}
.inner_project_description {
    font-size: 14px;
    color: #666;
    margin: 20px auto;
    text-align: left;
}
.inner_project_description span {
    display: block;
    margin-bottom: 15px;
}
.inner_project_content p {
    font-size: 14px;
    color: #999999;
    text-align: justify;
    direction: ltr;
}
.inner_project .project_cover {
    position: relative;
    cursor: pointer;
    margin-bottom: 25px;
}
.inner_project .project_cover a {
    display: -ms-grid;
    display: grid;
    position: relative;
}
.inner_project .project_cover a:after {
    content: '';
    position: absolute;
    left: 50%;
    top: 50%;
    width: 25px;
    height: 25px;
    opacity: 0;
    background-image: url(data:image/svg+xml;utf8;base64,);
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    transition: opacity .2s ease-out;
}
.inner_project .project_cover a:hover:after {
    opacity: 1;
    transition: opacity .2s ease-out;
    transition-delay: .25s;
}
.inner_project .project_cover a:before {
    content: '';
    position: absolute;
    left: 15px;
    right: 15px;
    top: 15px;
    bottom: 15px;
    -webkit-transform: scale(0, 0);
    transform: scale(0, 0);
    transition: -webkit-transform .2s ease-out;
    transition: transform .2s ease-out;
    transition: transform .2s ease-out, -webkit-transform .2s ease-out;
    background-color: rgba(255, 255, 51, 0.55);
}
.inner_project .project_cover a:hover:before {
    content: '';
    position: absolute;
    left: 15px;
    right: 15px;
    top: 15px;
    bottom: 15px;
    -webkit-transform: scale(1, 1);
    transform: scale(1, 1);
    transition: -webkit-transform .2s ease-out;
    transition: transform .2s ease-out;
    transition: transform .2s ease-out, -webkit-transform .2s ease-out;
    background-color: rgba(255, 255, 51, 0.55);
}
.inner_project .project_cover img {
    max-height: 400px;
    width: 100%;
    -o-object-fit: cover;
       object-fit: cover;
    -o-object-position: center;
       object-position: center;
}
.project_gallery {
    margin: 10px auto;
}
.project_gallery:after {
    content: '';
    display: block;
    clear: both;
}
.grid-col {
    margin-bottom: 30px;
}
.grid-item {
    cursor: pointer;
    position: relative;
    display: block;
}
.grid-item img {
    display: block;
    width: 100%;
    max-height: 300px;
    min-height: 300px;
    -o-object-fit: cover;
       object-fit: cover;
    -o-object-position: center;
       object-position: center;
}
.grid-item:after {
    content: '';
    position: absolute;
    left: 50%;
    top: 50%;
    width: 25px;
    height: 25px;
    opacity: 0;
    background-image: url(data:image/svg+xml;utf8;base64,);
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    transition: opacity .2s ease-out;
}
.grid-item:hover:after {
    opacity: 1;
    transition: opacity .2s ease-out;
    transition-delay: .25s;
}
.grid-item:before {
    content: '';
    position: absolute;
    left: 15px;
    right: 15px;
    top: 15px;
    bottom: 15px;
    -webkit-transform: scale(0, 0);
    transform: scale(0, 0);
    transition: -webkit-transform .2s ease-out;
    transition: transform .2s ease-out;
    transition: transform .2s ease-out, -webkit-transform .2s ease-out;
    background-color: rgba(255, 255, 51, 0.55);
}
.grid-item:hover:before {
    content: '';
    position: absolute;
    left: 15px;
    right: 15px;
    top: 15px;
    bottom: 15px;
    -webkit-transform: scale(1, 1);
    transform: scale(1, 1);
    transition: -webkit-transform .2s ease-out;
    transition: transform .2s ease-out;
    transition: transform .2s ease-out, -webkit-transform .2s ease-out;
    background-color: rgba(255, 255, 51, 0.55);
}



