
.inner_about_header {
    max-height: 400px;
    width: 100%;
    -o-object-fit: cover;
       object-fit: cover;
    -o-object-position: center;
       object-position: center;
}
.about_title {
    font-size: 24px;
    color: #888;
    font-family: "IRANSANS BOLD";
    margin: 40px auto;
    line-height: 1;
    text-align: left;
    position: relative;
    padding-left: 20px;
}
.about_title:before {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    width: 4px;
    background-color: #EDED43;
}
.about_desc {
    font-size: 14px;
    color: #999999;
    text-align: justify;
    margin-bottom: 40px;
}
.about_item img {
    max-height: 400px;
    width: 100%;
    -o-object-fit: cover;
       object-fit: cover;
    -o-object-position: top center;
       object-position: top center;
}
.about_item .person_name {
    color: #777;
    font-size: 1.2em;
    text-align: center;
}
.about_item .person_position {
    color: #999;
    font-size: 16px;
    text-align: center;
}



